@import url(https://fonts.googleapis.com/css?family=Share+Tech+Mono);
@import url(//fonts.googleapis.com/css?family=Old+Standard+TT);
@import url(//fonts.googleapis.com/css?family=Lobster+Two);
@font-face {
  font-family: 'Digital';
  src: url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot');
  src: url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot?#iefix')
      format('embedded-opentype'),
    url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff2')
      format('woff2'),
    url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff')
      format('woff'),
    url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.ttf')
      format('truetype'),
    url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.svg#Digital-7')
      format('svg');
}
h1{
  text-align: center;
  margin-top: 2%;
  font-family: 'Lobster Two', Verdana;
  color:#00e7ee;
  font-size: 4em;
}

#app {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

body {
  background: #010113;
  -webkit-touch-callout: none;
  -ms-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  cursor: default;
}

.calculator {
  border: 2px solid #47476b;
  padding: 5px;
  background: #90b9b6;
  width: 320px;
  position: relative;
  margin: auto;
}

.formulaScreen {
  min-height: 20px;
  font-family: 'Old Standard TT',digital;
  font-size: 20px;
  color: #875800;
  text-align: right;
  vertical-align: text-top;
  line-height: 20px;
  overflow-wrap: break-word;
  word-wrap: break-word;
}

.outputScreen {
  font-size: 29px;
  font-family: 'Old Standard TT',digital;
  color: black;
  text-align: right;
  line-height: 35px;
}

button {
  position: relative;
  height: 65px;
  width: 80px;
  color: black;
  outline: 1px solid black;
  border: none;
  background: white;
  font-family: 'Old Standard TT', monospace;
  font-size: 20px;
  cursor: default;
}
button:hover {
  color: black;
  outline: 0.05em solid grey;
  z-index: 3;
}

.jumbo {
  width: 160px;
}

.chat{
  background: #90b9b6;
  width: 20em;
  height: auto;
  color:black;
  padding: 2%;
}
ul li{
  list-style-type: none;
  margin-left: -12%;
  line-height: 1.6em;
}
.cont{
  display:flex;
  justify-content: space-around;
}
h3{
  font-style:italic;
  color:#646464;
}

